.App {
  text-align: center;
  background: linear-gradient(to right bottom, steelblue, white);
  font-family: 'Play', sans-serif;
  font-weight: bold 700;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.titulo{
  font-size:20px;
}

.pokebola{
  width: 30px;

}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.loading {
  align-content: center;
  margin-left: 50%;
}

button {
  border: 2px solid black;
  background: linear-gradient(to bottom, red 50%, white 50%);
  margin: 10px;
  width: 100px;
  padding: 8px;
  border-radius: 6px;
  font-family: 'Play', sans-serif;
  font-weight: bold;
}

.lista {
  justify-items: right;
  align-self: center;
  text-align: left;
  border: 2px solid rgb(107, 107, 107);
  background: linear-gradient(to right bottom, grey, rgb(209, 209, 209));
  padding-left: 8px;
  font: bold;
  width: 15%;
  margin-top: 20px;
  margin-left: 10%;
}



.tarjeta {
  width: 30%;
  max-width: 300px;
  min-width: 200px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: 40%;
  border: 4px solid rgb(39, 39, 39);
  border-radius: 8px;
  background: linear-gradient(to bottom, red 50%, white 50%);
  float: right;
  font-family: 'Play', sans-serif;
  font-weight: bold;
}

.imagenPokemon {
  border: 2px solid rgb(39, 39, 39);
  border-radius: 8px;
  display: block;
  margin: auto;
  width: 150px;
  min-width: 100px;
  margin-top: 20px;
  background: radial-gradient(rgb(253, 253, 8), rgb(148, 148, 31));
}
